var mp = mp || {};

// Form validation
//----------------------------------------------------------
mp.formCart = function() {
    var $form = $('#form-cart');
        $submitBtn = $form.find('button.btn');

    if (!$form.length) {
        return;
    }

    $form.validate({
        rules: {
            "name": {
                required: true,
                minlength: 4
            },
            "email": {
                required: true,
                email: true
            },
            "address": {
                required: true,
                minlength: 4
            },
            "postal_code": {
                required: true,
                minlength: 5
            },
            "city": {
                required: true,
                minlength: 2
            },
            "phone": {
                required: true,
                minlength: 6
            },
            "consent": {
                required: true,
                minlength: 1
            },
        },
        errorPlacement: function(error, element) {

        },

        invalidHandler: function(event, validator) {
            // $submitBtn.toggleClass('disabled');
        },

        highlight: function(element, errorClass, validClass) {
            $(element).addClass(errorClass);
            $(element).parents('.form__group').addClass(errorClass);
        },

        unhighlight: function(element, errorClass, validClass) {
            $(element).removeClass(errorClass);
            $(element).parents('.form__group').removeClass(errorClass);
        },
    });
};



$(document).ready(function (){
    mp.formCart();
});