var mp = mp || {};

// Slider - Hero
//-----------------------------------------------------------------
mp.sliderHero = function() {
    var $slider = $('.hero__slider');

    if (!$slider.length) {
        return;
    }

    $slider.slick({
        arrows: false,
        dots: false,
        speed: 2000, 
        pauseOnFocus: false,
        pauseOnHover: false,
        infinite: true,
        slidesToShow: 1,
        autoplay: true,
        autoplaySpeed: 6000,
        fade: true,
        prevArrow: '<button type="button" class="slick-prev"><i class="icon-chevron-left"></i></button',
        nextArrow: '<button type="button" class="slick-next"><i class="icon-chevron-right"></i></button',
    });
}





// Slider - Products Single
//-----------------------------------------------------------------
mp.sliderProductsSingle = function() {
    var $slider = $('.products__single .slider__main');

    if (!$slider.length) {
        return;
    }

    $slider.slick({
        arrows: true,
        dots: false,
        speed: 2000, 
        pauseOnFocus: false,
        pauseOnHover: false,
        infinite: true,
        slidesToShow: 1,
        autoplay: false,
        fade: true,
        prevArrow: '<button type="button" class="slick-prev"><i class="icon-circle-left"></i></button',
        nextArrow: '<button type="button" class="slick-next"><i class="icon-circle-right"></i></button',
    });
}


$(document).ready(function (){
    mp.sliderHero();
    mp.sliderProductsSingle();
});